@import "../../index.scss";

.features-container {
  &__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    margin: 1rem;

    &-title {
      flex: 1;
      max-width: 180px;
      margin: 0 2rem 0 0;

      h1 {
        font-weight: 800;
        font-size: 18px;
        line-height: 24px;
        letter-spacing: -0.04em;
        color: $color-nav;
      }

      div {
        width: 38px;
        height: 3px;
        background: $gradient-bar;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        margin-bottom: 0.3rem;
      }
    }

    &-text {
      flex: 2;
      max-width: 390px;
      display: flex;

      p {
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
        color: $color-text;
      }
    }
  }
}

@media screen and (max-width: 550px) {
  .features-container__feature {
    margin: 1rem 0;

    &-title h1 {
      font-size: 14px;
      line-height: 22px;
    }

    &-text p {
      font-size: 12px;
      line-height: 20px;
    }
  }
}
  